const dependencies = {
  path: require('path'),
  chalk: require('chalk'),
  slugify: require('slugify'),
  BbPromise: require('bluebird'),
  Runtimes: require('./runtimes'),
  ZipService: require('./package/ZipService'),
  LayersService: require('./aws/LayersService'),
  BucketService: require('./aws/BucketService'),
  LocalFolders: require('./package/LocalFolders'),
  Dependencies: require('./package/Dependencies'),
  CloudFormationService: require('./aws/CloudFormationService'),
  checkRequirements: require('./plugin/requirements'),
  DeployCommand: require('./commands/Deploy')
};

async function init() {
  console.log('init func');
}

module.exports = ($sls, injections) => {
  const { service } = $sls;

  // pre validation
  checkRequirements($sls);

  const {
    path,
    BbPromise,
    DeployCommand,
    checkRequirements,
  } = { ...dependencies, ...injections };

  /**
   * Default options
   */
  const defaultOpts = {
    path: '.',
    functions: null,
    dependencyInstall: true,
    compileDir: '.serverless',
    customInstallationCommand: null,
    layersDeploymentBucket: service.provider.deploymentBucket
  };

  // const inboundSettings = (service.custom || {})['serverless-layers'];

  return {
    hooks: {
      /**
       * @fires before:package:function:package
       */
      'before:package:function:package': () =>
        BbPromise.bind(this).then(() => DeployCommand($sls, { defaultOpts })),
      'before:package:initialize': () =>
        BbPromise.bind(this).then(() => DeployCommand($sls, { defaultOpts })),
    }
  }
};


